.Chat {
    flex: 0.70;
    background-color: steelblue;
    overflow: hidden;
    height: 90vh;
}

.chat_header {
    height: 5vh;
    margin: 10px;
    margin-left: 20px;
    object-fit: contain;
    overflow: hidden;
    align-items: center;
}

.chat_header_info {
    object-fit: contain;
    display: flex;
    flex: 1;
}

.chat_header_info>img {
    object-fit: contain;
    height: 50px;
}

.grp_info {
    display: flex;
    margin-left: 30px;
    justify-content: space-evenly;
}

.chat_body {
    z-index: -1;
    height: 70vh;
    overflow: scroll;
    padding: 30px;
    background-color: thistle;
    /* background-image: url("https://images.pexels.com/photos/919700/pexels-photo-919700.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500"); */
    background-position: center;
    background-repeat: no-repeat;
    background-size: fill;
}

.chat_body::-webkit-scrollbar {
    display: none;
}

.chat_message {
    position: relative;
    font-size: 18px;
    padding: 10px;
    border-radius: 10%;
    width: fit-content;
    background-color: white;
    font-weight: normal;
}

.chat-receiver {
    margin-left: auto;
    background-color: teal;
}

.chat_name {
    position: absolute;
    top: -15px;
    text-align: left;
    font-size: xx-small;
    font-weight: 800;
}

.chat_timestamp {
    margin-left: 10px;
    font-size: xx-small;
    font-weight: 800;
}

.chat_input {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 52px;
}

.chat_input {
    flex: 0.80;
    display: flex;
    align-items: center;
    background-color: lightgray;
    justify-content: space-between;
}

.chat_input>form {
    flex: 0.8;
    display: flex;
}

.chat_input>form>input {
    outline: none;
    border: none;
    flex: 1;
    font-size: 18px;
    border-radius: 30px;
    padding: 10px;
    background-color: white;
    color: black;
}

.chat_input>form>input:focus {
    border: none;
}

.chat_input>form>button {
    display: none;
}