.SidebarChat {
    padding: 10px;
    height: fit-content;
    display: flex;
    cursor: pointer;
    border-bottom: 1px solid #f6f6f6;
}

.SidebarChat:hover {
    background-color: #ebebeb;
    transition: all 250ms cubic-bezier(0.165, 0.84, 0.44, 1);
}

.SidebarChat>img {
    height: 50px;
}

.sidechat_bar_info>p {
    color: black;
    text-decoration: none;
}

.sidechat_bar_info {
    display: block;
    margin-left: 20px;
}

.addChat {
    cursor: pointer;
}

.addChat:hover h2 {
    text-decoration: underline;
}