.Login {
    height: 100vh;
    overflow: hidden;
    width: 100vw;
    background-color: #1b262c;
    text-align: center;
    justify-content: center;
}

.container {
    justify-content: center;
    position: relative;
}

.logo img {
    object-fit: contain;
    width: 70vw;
    height: 50vh;
}

.signin {
    margin-top: 10vh;
    text-align: center;
    position: relative;
    display: inline-block;
}

.signin>p {
    font-size: 18px;
    font-weight: 600;
    color: white;
    padding: 7px 17px;
    cursor: pointer;
    border-radius: 3px;
    background-color: #00a859;
}

.signin>p:hover {
    transition: all 250ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
    background-color: #00a85aa6;
}

.Dev_by {
    position: relative;
    margin: 0px;
    display: block;
    text-align: center;
    color: rgb(216, 216, 216);
    justify-content: center;
}