.home {
    height: 100vh;
    margin: 0;
    width: 100vw;
    overflow: hidden;
    background-color: #1b262c;
    display: grid;
    place-items: center;
    justify-content: center;
}

.Chat_sec {
    margin-top: 30px;
    height: 90vh;
    width: 90vw;
    display: flex;
    overflow: hidden;
    background-color: white;
    border-radius: 3px;
}

.signout {
    position: relative;
    float: right;
}

.signout>p {
    font-size: 18px;
    color: white;
    font-weight: 600;
    border-radius: 3px;
    padding: 7px 17px;
    background-color: #00a859;
}

.signout>p:hover {
    transition: all 250ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
    background-color: #00a85aa6;
}