.sidebar {
    flex: 0.30;
    overflow: hidden;
}

.sidebar_header {
    display: flex;
    justify-content: left;
    padding: 15px;
    height: 40px;
    object-fit: contain;
}

.sidebar_header img {
    object-fit: contain;
    height: 40px;
}

.sidebar_header>p {
    font-size: 18px;
    font-weight: 600;
    margin-left: 20px;
}

.sidebar_search {
    width: 100%;
    padding: 7px;
    padding-top: 20px;
}

.sidebar_search>input {
    width: 85%;
    border: none;
    outline: none;
    font-size: 18px;
    color: black;
    border-radius: 99px;
    padding: 7px 17px;
    background-color: lightblue;
    border-bottom: 2px solid transparent;
}

.sidebar_search>input:focus {
    border-bottom: 2px solid black;
}

.sidebar_chats {
    margin-top: 20px;
    overflow-y: scroll;
    flex: 1;
    height: 100%;
    text-align: center;
    align-items: center;
}

.sidebar_chats::-webkit-scrollbar-thumb {
    background-color: pink;
}